<template >
    <div class="fancy">
        <transition appear :enter-active-class="enterclass" :leave-active-class="leaveclass" >
              <h4 v-if="show" class="FancyBig">{{Text[i]}}</h4>
        </transition>
        <transition appear :enter-active-class="enterclass" :leave-active-class="leaveclass" >
              <h4 v-if="showWholeText" class="FancySmall">{{ShowText}}</h4>
        </transition>
    </div>
</template>
<script>
// JS
export default  {
    props:['leaveclass','enterclass','text','alternate','x'],
    mounted(){
        if(this.alternate){
            this.ShowText=this.alternate
        }
        else{
            this.ShowText=this.text
        }
        if(this.x){
            this.times=this.x;
        }
        else{
            this.times=1;
        }
        this.Text = this.text.split(" ");
        this.intval=setInterval(function(){
            this.keepChanging();
        }.bind(this), 3500);
    },
    methods:{
        keepChanging:function() {

            //let Text = this.text.split(" ");
            this.changeShow();
            
            if(this.i<this.Text.length-1){
                this.i++;
            }
            else{
                this.times++;
                this.i=0;
            }
            setTimeout(function(){
                this.changeShow();
            }.bind(this), 2000);
        },
        sleep:function( sleepDuration ){
            var now = new Date().getTime();
            while(new Date().getTime() < now + sleepDuration){ /* do nothing */ } 
            },
        changeShow:function(){
            if(this.times==2){
                clearInterval(this.intval);
                this.show=undefined;
                this.showWholeText=1;
                this.$emit("stopped");
                return;
            }
            if(this.show){
                this.show=undefined;
            }
            else{
                this.show=1;
            }
        }
    },
    data(){
        return{
            i:0,
            Text:[],
            show:1,
            intval:0,
            times:0,
            showWholeText:undefined,
            ShowText:""
        }
    }
}
</script>
<style lang="scss" scoped>
    .FancyBig{
        font-size:7rem;
        font-weight:990;
        text-align:center;
    }
    .FancySmall{
        font-size:4rem;
        font-weight:990;
        text-align:center;
        break-word:break-all;

        &:hover{
            animation:swing;
            animation-duration:1s;
        }
    }
</style>