<template >
    <div class="card" :class="classes">
        <div class="card-image" v-if="img" @mouseover='activate' >
            <div ref="activator" class="activator"></div>
            <slot name="header"></slot>
        </div>
        <div class="card-content" @mouseover='activate' >
            <slot name="body"></slot>
        </div>
        <div class="card-action" v-if="foot" @mouseover='activate' >
            <slot name="footer"></slot>
        </div>
        <div class="card-reveal" v-if="reveal" @mouseleave='deactivate'>
            <span ref="deactivator" class="card-title "></span>
            <slot name="reveal">
            </slot>
        </div>
    </div>
</template>
<script>
// JS
export default  {
    props:['classes','img','foot','reveal'],
    methods:{
        activate:function(params) {
            if(this.reveal){
                this.$refs.activator.click()
            }
            
        },
        deactivate:function(params){
            if(this.reveal){
                this.$refs.deactivator.click()
            }
            
        }
    }
}

</script>
